import faunadb from 'faunadb';
const client = new faunadb.Client({
  secret: 'fnAFRwOe3oAAQhSzeyAP3UrEao22VcGoUKJo0RVO',
});
const q = faunadb.query;

export { client, q };

export const getAllWishes = client
.query(q.Paginate(q.Match(q.Ref('indexes/all_wishes3'))))

  .then((response) => {
    const expenseRef = response.data;
    const getAllDataQuery = expenseRef.map((ref) => {
      return q.Get(ref);
    });
    return client.query(getAllDataQuery).then((data) => data);
  })
  .catch((error) => console.error('Error: ', error.message));

export const createWish = ({ name, description, wishes }) =>
  client
    .query(
      q.Create(q.Collection('Wishes'), {
        data: {
          name,
          description,
          wishes,
        },
      }),
    )
    .then((ret) => ret)
    .catch((error) => console.error('Error: ', error.message));
